import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Table, InputGroup, FormControl, Spinner, ToggleButton, ToggleButtonGroup } from 'react-bootstrap';

export class DataRenderer extends Component {
  constructor(props) {
    super(props)
    this.state ={loading: false, city: "", favCities: []}
    this.typingTimeout = null;
    this.onFieldChange = this.onFieldChange.bind(this);
  }
  createFavoriteCities = async() => {
    const cities = ["New York City", "Chicago", "Houston", "Los Angeles", "San Diego", "City of London", "New Dehli"]
    let things = []
    for (let i = 0; i < cities.length; i++) {
      const city = cities[i]
      switch(city) {
        case "New York City":
          things.push(<ToggleButton key={city} value={city} onClick={() => {this.setState({city: city}); this.onFieldChange(city);}}>NYC</ToggleButton>);
          break;
        case "Los Angeles":
          things.push(<ToggleButton key={city} value={city} onClick={() => {this.setState({city: city}); this.onFieldChange(city);}}>LA</ToggleButton>);
          break;
        case "City of London":
          things.push(<ToggleButton key={city} value={city} onClick={() => {this.setState({city: city}); this.onFieldChange(city);}}>London</ToggleButton>);
          break;
        default:
          things.push(<ToggleButton key={city} value={city} onClick={() => {this.setState({city: city}); this.onFieldChange(city);}}>{city}</ToggleButton>);
          break;
      } 
    }
    this.state.favCities = things;
    return things
  }
  createGroup = async (city) => {
    this.setState({Times: [], DayRank: [], Header: [], CityString: [], NextGoodDay: []})
    let group = []
    let header = []
    const API_HOST= "sundara-api.devksingh.com"
    const todayDate = new Date().toLocaleDateString('en-GB')
    let cityString = ``
    await fetch(`https://${API_HOST}/api/getCityCode?city=${city}`)
      .then(async (response1) => {
          response1 = response1.json();
          return response1
      }).then(async (resp) => {
          const citycode = resp.id
          cityString = `${resp.city}, ${resp.state} (${resp.country})`
          await fetch(`https://${API_HOST}/api/getTimes?citycode=${citycode}&date=${todayDate}`)
          .then(async (response) => {
            response = await response.json();
              header.push(<thead><tr><th>Good Time Start</th><th>Good Time End</th></tr></thead>)
            for (let i = 0; i < response.goodTimes.length; i++) {
              if (response.goodTimes[i][0] === response.goodTimes[i][1]){
                continue
              }
              group.push(
                <tr key={i}><td>{response.goodTimes[i][0]}</td><td>{response.goodTimes[i][1]}</td></tr>
              )
            }
            let rank = "";
            if (response.dayRanking === "Good") {
              rank = <h2 style={{color: 'green'}}>Today is a Good day <span role="img" aria-label="happy">üòä</span></h2>
            } else if (response.dayRanking === "Bad") {
              rank = <h2 style={{color: 'red'}}>Today is a Bad day <span role="img" aria-label="sad">üòû</span></h2>
            } else {
              rank = <h2>Today is an OK day.</h2>
            }
            if (response.dayRanking !== "Good") {
              let tempResp = response.dayRanking;
              let tomorrow = new Date();
              let tommdate = "";
              let i = 0
              do {
                tomorrow.setDate(tomorrow.getDate() + i);
                tommdate = tomorrow.toLocaleDateString('en-GB');
                await fetch(`https://${API_HOST}/api/getTimes?citycode=${citycode}&date=${tommdate}`)
                // eslint-disable-next-line
                .then(async (response) => {
                  response = await response.json();
                  tempResp = response.dayRanking
                })
                i += 1
              } while (tempResp !== "Good")
              const humanDate = new Date(tommdate).toDateString();
              const nextGoodDayElem = <h4><i>Next good day is: {humanDate}.</i></h4>
              this.setState({NextGoodDay: nextGoodDayElem})
            }
            this.setState({Times: group, DayRank: rank, Header: header, CityString: <i>{cityString}</i>})
        })
      }).catch(() => {
        if (this.state.city !== '') {
          this.setState({Times: [], DayRank: <h2 style={{color: 'red'}}>City not found</h2>, Header: [], CityString: []})
        } else {
          this.setState({Times: [], DayRank: <h2>Please enter a city</h2>, Header: [], CityString: []})
        }
      })
    }
    doTheThing = async (d) => {
      await this.createGroup(d); this.setState({loading: false});
    }
    onFieldChange = async (meme) => {
      await this.setState({loading: true, city: meme});
      await clearTimeout(this.typingTimeout);
      this.typingTimeout = setTimeout(() => {this.doTheThing(meme)}, 550);
    }
    render() {
      this.createFavoriteCities();
      return (
      <Container>
          <InputGroup className="mb-3">
            <InputGroup.Prepend>
              <InputGroup.Text id="basic-addon3">
                City
              </InputGroup.Text>
            </InputGroup.Prepend>
            <FormControl onChange={(event) => {console.log(event.target.value); this.onFieldChange(event.target.value)}} id="geocoderid" value={this.state.city} aria-describedby="basic-addon3" />
          </InputGroup>
          <i>Or choose from some common cities</i>
          <br />
          <ToggleButtonGroup type="radio" name="options">
            {this.state.favCities}
          </ToggleButtonGroup>
          <br />
          <br />
        <div aria-label="Times list" key={'opts'}>
          {this.state.loading ? <Spinner animation="border" /> : 
          <div>
              {this.state.DayRank} 
              {this.state.NextGoodDay} 
              {this.state.CityString}           
              <br /><Table striped bordered hover>
              {this.state.Header}
              <tbody>
                {this.state.Times}
              </tbody>
            </Table>
          </div>
          }
          {this.state.loading ? <span></span> : <hr />}
        </div>
      </Container>
      );
    }
}